body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

i.fas,
i.fab {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1em;
  padding: 6px;
  transition: 0.2s;
}
i.fas:hover,
i.fab:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  color: white;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --duration1: 1000ms;
  --duration2: 600ms;
}
/* FADE */
/* appear - on page load */

.fade-appear {
  opacity: 0;
  z-index: 1;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 1000ms linear;
  transition: opacity var(--duration1) linear;
}

/* enter */
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms linear;
  transition: opacity var(--duration1) linear;
}

/* EXIT */
.fade-exit {
  opacity: 0;
}

.fade-exit.fade-exit-active {
  opacity: 1;
  transition: opacity 1000ms ease-out;
  transition: opacity var(--duration1) ease-out;
}

/* FADE2 */
/* appear - on page load */

.fade-appear {
  opacity: 0;
  z-index: 1;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 600ms linear;
  transition: opacity var(--duration2) linear;
}

/* enter */
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 600ms linear;
  transition: opacity var(--duration2) linear;
}

/* EXIT */
.fade-exit {
  opacity: 0;
}

.fade-exit.fade-exit-active {
  opacity: 1;
  transition: opacity 600ms ease-out;
  transition: opacity var(--duration2) ease-out;
}

/* SLIDE */

/* appear - on page load */

.slide-appear {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.slide-appear-active {
  -webkit-animation: slide-in 600ms ease-out forwards;
          animation: slide-in 600ms ease-out forwards;
  -webkit-animation: slide-in var(--duration2) ease-out forwards;
          animation: slide-in var(--duration2) ease-out forwards;
}

/* enter */
.slide-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.slide-enter-active {
  -webkit-animation: slide-in 600ms ease-out forwards;
          animation: slide-in 600ms ease-out forwards;
  -webkit-animation: slide-in var(--duration2) ease-out forwards;
          animation: slide-in var(--duration2) ease-out forwards;
}

/* EXIT */
.slide-exit {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.slide-exit-active {
  -webkit-animation: slide-out 600ms ease-out forwards;
          animation: slide-out 600ms ease-out forwards;
  -webkit-animation: slide-out var(--duration2) ease-out forwards;
          animation: slide-out var(--duration2) ease-out forwards;
}

@-webkit-keyframes slide-in {
  from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
  }
}

@keyframes slide-in {
  from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
  }
}

@-webkit-keyframes slide-out {
  from {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

@keyframes slide-out {
  from {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

